@font-face {
  font-family: "Avenir";
  src: url("../fonts/Avenir-Black.woff2") format("woff2"), url("../fonts/Avenir-Black.woff") format("woff");
  font-weight: 900;
  font-style: normal; }

@font-face {
  font-family: "Avenir";
  src: url("../fonts/Avenir-Heavy.woff2") format("woff2"), url("../fonts/Avenir-Heavy.woff") format("woff");
  font-weight: 800;
  font-style: normal; }

@font-face {
  font-family: "Avenir";
  src: url("../fonts/Avenir-Medium.woff2") format("woff2"), url("../fonts/Avenir-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

body {
  font-family: "Avenir", "Arial", sans-serif;
  box-sizing: border-box;
  min-width: 320px;
  margin: 0; }

img {
  max-width: 100%;
  height: auto; }

a {
  text-decoration: none; }

ul {
  list-style-type: none;
  padding: 0;
  margin: 0; }

.header {
  background-color: #161851;
  background-attachment: fixed;
  height: 100%; }

.header__wrap {
  padding: 30px 5%;
  display: flex;
  justify-content: space-between;
  align-items: center; }

.header__menu {
  width: 45%;
  max-width: 200px;
  display: flex;
  justify-content: space-between;
  align-items: center; }

.header__contact {
  display: flex;
  width: 45%;
  max-width: 200px;
  justify-content: space-between;
  align-items: baseline;
  flex-shrink: 1; }

@media (min-width: 768px) {
  .header__menu {
    max-width: 250px;
    align-items: center; }
  .header__contact {
    max-width: 450px; } }

@media (min-width: 1300px) {
  .header__menu {
    max-width: 270px; }
  .header__contact {
    max-width: 600px; }
  .header__wrap {
    max-width: 1300px;
    margin: 0 auto; } }

.contact__social-icon {
  align-items: center;
  justify-content: space-between;
  width: 33%;
  max-width: 150px;
  display: none; }

.contact__telephone {
  font-weight: 500;
  font-size: 12px;
  color: #ffffff; }

@media (min-width: 768px) {
  .contact__social-icon {
    display: flex; }
  .contact__telephone {
    font-size: 16px; } }

@media (min-width: 1300px) {
  .contact__telephone {
    font-size: 18px; } }

@media (min-width: 768px) {
  .menu__logo {
    width: 172px; }
  .menu__hamburger {
    width: 52px;
    height: 42px; } }

.evision {
  background-color: #161851;
  /*background-attachment: fixed;
  height: 100%;
  position: relative;*/
  position: relative;
  height: 100vh;
  background-attachment: fixed; }
  .evision::after {
    /*content: "";*/
    position: absolute;
    top: 0;
    left: 50%;
    background-image: url("../img/circle.png");
    width: 744px;
    min-height: 557px;
    opacity: 0.3;
    background-repeat: no-repeat;
    background-position: center bottom;
    margin-left: -372px;
    background-color: #161851;
    z-index: -1; }

.evision__smart {
  font-weight: 900;
  color: #ffffff;
  text-align: center;
  font-size: 26px;
  line-height: 32px;
  margin: 0; }

.evision__list {
  margin-top: 55px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center; }

@media (min-width: 768px) {
  .evision__smart {
    font-size: 32px; } }

@media (min-width: 1300px) {
  .evision__list {
    margin: 96px auto 0 auto;
    max-width: 790px; }
  .evision__smart {
    padding-top: 116px;
    font-size: 64px;
    line-height: 79px; } }

.skill__word {
  font-weight: 500;
  font-size: 18px;
  color: #ffffff;
  margin-bottom: 0; }

.skill__item {
  text-align: center;
  margin: 0 auto; }
  .skill__item:last-child {
    width: 100%; }

@media (min-width: 768px) {
  .skill__img {
    width: 140px;
    height: 140px; }
  .skill__item:last-child {
    width: auto; }
  .skill__word {
    font-size: 20px; } }

@media (min-width: 1300px) {
  .skill__img {
    width: 225px;
    height: 225px; }
  .skill__word {
    margin-top: 19px;
    font-size: 27px;
    line-height: 65px; } }

.product {
  background-color: gray; }

.product__item {
  min-height: 420px;
  padding: 30px 15px;
  text-align: center;
  border: 1px solid #e8e8ee;
  text-align: center; }

.carousel {
  width: 80%;
  padding: 10px; }

.product__image {
  object-fit: cover;
  overflow: hidden; }

.product__wrap {
  height: 25vh; }

.product__name {
  font-size: 22px;
  font-weight: 500;
  color: #ff6161;
  margin-top: 5vh; }

.product__more {
  color: #161851;
  font-weight: 500;
  font-size: 22px;
  height: 20vh; }

.product__link {
  text-align: center;
  display: block;
  font-weight: 900;
  font-size: 12px;
  color: #ffffff;
  background-color: #ff6161;
  margin: 0 auto;
  height: 6vh;
  padding-top: 1vh;
  cursor: pointer;
  max-width: 300px; }

@media (min-width: 768px) {
  .product__name {
    margin-top: 8vh; }
  .product__more {
    height: auto; }
  .product__link {
    max-width: 316px;
    margin-top: 5vh; } }

@media (min-width: 1300px) {
  .product__item {
    min-height: 0;
    max-height: 292px;
    max-width: 954px;
    margin: 0 auto;
    display: flex;
    align-items: flex-start;
    padding-top: 40px;
    padding-left: 60px;
    box-sizing: border-box; }
  .product__wrap {
    float: right; }
  .product__wrap {
    order: 2; }
  .product__more {
    font-size: 34px;
    line-height: 43px; }
  .product__wrap-text {
    text-align: left; }
  .product__link {
    margin: 0;
    width: 320px;
    height: 70px;
    text-align: center;
    font-size: 12px;
    padding-top: 27px; }
  .product__name {
    font-size: 34px;
    line-height: 43px;
    margin-top: 0; } }
